import React from "react"
import Layout from "../components/layout"
//import { graphql } from "gatsby"
import { Link, graphql } from "gatsby"

export default ({ data }) => {

  console.log(data)
  //<div> //replace with centred column of text
  //<div style={{ margin: `3rem auto`, maxWidth: 600 }}> //replace with layout so we can reuse on other pages

return (
  <Layout>
    <p>Building a fake Gatsby site!</p>
    <p>
      What is this? A test website with various place holder demos made using Unity game engine and other random things.
    </p>

	<h4>{data.allMarkdownRemark.totalCount} Posts</h4>
        {data.allMarkdownRemark.edges.map(({ node }) => (
          <div key={node.id}>
	    <Link
              to={node.fields.slug}
            >

            <h3>
              {node.frontmatter.title}{" "}
              <span>
                â€” {node.frontmatter.date}
              </span>
            </h3>
            <p>{node.excerpt}</p>

	    </Link>
          </div>
        ))}

  </Layout>
)

}

// To sort posts in descending order, by date replace: 
//	allMarkdownRemark {
// with 
// 	allMarkdownRemark(sort: { fields: [frontmatter___date], order: DESC }). 
// Note: There are 3 underscores between frontmatter and date. 


export const query = graphql`
  query {
    allMarkdownRemark (sort: { fields: [frontmatter___date], order: DESC }) {
      totalCount
      edges {
        node {
          id
          frontmatter {
            title
            date(formatString: "DD MMMM, YYYY")
          }
          fields {
            slug
          }
          excerpt
        }
      }
    }
  }
`


